import React from 'react';
import LinkButton from '../Global/LinkButton';
import HeaderImg from '../../images/header-img.png';

const HeaderMainContent = () => {
    return (
        <>
            <div className="header-main__left">
                <div className="header-main__left-title">
                    Want anything to be easy with <span className="bolderText">LaslesVPN.</span> 
                </div>

                <div className="header-main__left-subtitle">
                    Provide a network for all your needs with ease and fun using 
                    <span className="bolderText"> LaslesVPN</span> discover interesting features from us.
                </div>

                <div className="header-main__left-getStarted">
                    <LinkButton text={"Get Started"}/>
                </div>
            </div>

            <div className="header-main__right">
                <img src={HeaderImg} alt="img"/>   
            </div>
        </>
    )
}

export default HeaderMainContent;