import React, { useEffect } from 'react';
import Swiper from 'swiper/bundle';
import 'swiper/swiper-bundle.css';
import {Reviews} from './Reviews';
import StarIco from '../../images/star.svg'


const SliderItem = ({user}) => {
    return (
        <div className="sliderSection__item swiper-slide">
            <div className="sliderSection__item-top">
                <div className="sliderSection__item-top-left">
                    <img 
                        className="sliderSection__item-top-left-img"
                        src={user.img} 
                        alt="userImg"
                    />
                </div>
                
                <div className="sliderSection__item-top-center">
                    <div className="sliderSection__item-top-center-fullName">
                        {user.fullName}
                    </div>
                    <div className="sliderSection__item-top-center-region">
                        {user.region}
                    </div>
                </div>
                <div className="sliderSection__item-top-right">
                    <span className="sliderSection__item-top-right-rate">
                        {user.rate}
                    </span>      
                    <img 
                        className="sliderSection__item-top-right-img"
                        src={StarIco} alt="starIco"
                    />
                </div>
            </div>

            <div className="sliderSection__item-content">
                {user.review}
            </div>
        </div>
    )
}

const SliderSection = () => {
    useEffect(() => {
        // eslint-disable-next-line
        const swiper = new Swiper('.swiper-container', {
            slidesPerView: 'auto',
            spaceBetween: 50,
            pagination: {
                el: '.swiper-pagination',
                clickable: true,
            },
            navigation: {
                nextEl: '.swiper-button-nextCustom',
                prevEl: '.swiper-button-prevCustom',
            },
        });
    }, [])

    return (
        <section className="sliderSection sliderSection-outer">
            <div className="sliderSection-inner container">
                <div className="sliderSection-top">
                    <div className="sliderSection-top__title">
                        Trusted by Thousands of Happy Customer
                    </div>
                    <div className="sliderSection-top__subtitle">
                        These are the stories of our customers who have joined us 
                        with great pleasure when using this crazy feature.
                    </div>
                </div>
            
                <div className="sliderSection-slider swiper-container">
                    <div className="swiper-wrapper">
                            {Reviews.map(item => <SliderItem key={item.id} user={item}/>)}
                    </div>
                    <div className="swiper-nav">
                        <button className="swiper-button-prevCustom"></button>
                        <button className="swiper-button-nextCustom"></button>
                    </div>
                    <div className="swiper-pagination"></div>
                </div>
            </div>   
        </section>
    )
}

export default SliderSection;