import React from 'react';
import LinkButton from '../Global/LinkButton';

const SubscribeSection = () => {
    return (
        <section className="subscribe subscribe-outer">
            <div className="subscribe-inner container">
                <div className="subscribe__left">
                    <div className="subscribe__left-title">
                        Subscribe Now for Get Special Features!
                    </div>
                    <div className="subscribe__left-subtitle">
                        Let's subscribe with us and find the fun.
                    </div>
                </div>
                <div className="subscribe__right">
                    <LinkButton text={"Subscribe Now"}/>
                </div>
            </div>
        </section>
    )
}

export default SubscribeSection;