import React from 'react';
import PlanItemBox from '../../images/planItemBox.png';
import CheckBullet from '../../images/check.svg';
import LinkButton from '../Global/LinkButton';

const PlanData = [
    {
        plan: "Free Plan",
        list: [
            "Unlimited Bandwitch",
            "Encrypted Connection",
            "No Traffic Logs",
            "Works on All Devices"
        ],
        tariff: "Free",
        mo: false,
        id:Math.floor(Math.random() * 1000) 
    },
    {
        plan: "Standard Plan",
        list: [
            "Unlimited Bandwitch",
            "Encrypted Connection",
            "Yes Traffic Logs",
            "Works on All Devices",
            "Connect Anyware"
        ],
        tariff: "$9",
        mo: true,
        id:Math.floor(Math.random() * 1000) 
    },
    {
        plan: "Premium Plan",
        list: [
            "Unlimited Bandwitch",
            "Encrypted Connection",
            "Yes Traffic Logs",
            "Works on All Devices",
            "Connect Anyware",
            "Get New Features"
        ],
        tariff: "$12",
        mo: true,
        id:Math.floor(Math.random() * 1000) 
    }
]

const PlanItem = (props) => {
    return (
        <div className="plan-main__item">
            <img className="plain-main__item-img" src={PlanItemBox} alt="box"/>
            <div className="plan-main__item-title">{props.data.plan}</div>
            <div className="plan-main__item-list">
                {props.data.list.map((item, index) => (
                    <div key={index} className="plan-main__item-list-item">
                        <img 
                            className="plan-main__item-list-item-img" 
                            src={CheckBullet} alt="check"
                        />
                        <div className="plan-main__item-list-item-text">
                            {item}
                        </div>
                    </div>
                ))}
            </div>
            <div className="plan-main__item-tariff">
                <span className="plan-main__item-tariff-price">
                    {props.data.tariff} 
                </span>
                <span className="plan-main__item-tariff-mo">
                    {props.data.mo ? <> / mo</> : null}
                </span>
            </div>
            <div className="plan-main__item-select">
                <LinkButton text={"Select"}/>
            </div>
        </div>
    )
}

const PlanSection = () => {
    return (
        <section className="plan plan-outer">
            <div className="plan-inner container">
                <div className="plan-top">
                    <div className="plan-top__title">
                        Choose Your Plan
                    </div>
                    <div className="plan-top__subtitle">
                        Let's choose the package that is best for you 
                        and explore it happily and cheerfully.
                    </div>
                </div>

                <div className="plan-main">
                    {
                        PlanData.map(item => <PlanItem key={item.id} data={item}/>)
                    }
                </div>
            </div>
        </section>
    )
}

export default PlanSection;