import HeaderLogo from '../../images/logo.svg';

const Logo = () => {
    return (
        <div className="logo">
            <a className="logo-link" href="/">
                <img 
                    className="logo-link-img"
                    src={HeaderLogo} 
                    alt="logo"
                />  
            </a>         
        </div>
    )
}

export default Logo;