import React from 'react';

// Icons
import netflixIco from '../../images/netflix.svg';
import redditIco from '../../images/reddit.svg';
import spotifyIco from '../../images/spotify.svg';
import discordIco from '../../images/discord.svg';
import amazonIco from '../../images/amazon.svg';


const SponsorsSection = () => {
    return (
        <section className="sponsorsSection sponsorsSection-outer">
            <div className="sponsorsSection-inner container">
                <img className="sponsorsSection-ico" src={netflixIco} alt="netflixIco"/>
                <img className="sponsorsSection-ico" src={redditIco} alt="redditIco"/>
                <img className="sponsorsSection-ico" src={amazonIco} alt="amazonIco"/>
                <img className="sponsorsSection-ico" src={discordIco} alt="discrodIco"/>
                <img className="sponsorsSection-ico" src={spotifyIco} alt="spotifyIco"/>
            </div>
        </section>
    )
}

export default SponsorsSection;