import React from 'react';
import Logo from '../Global/Logo';
import HeaderNav from './HeaderNav';
import SignInAndUp from './SignInAndUp';
import HeaderMainContent from './HeaderMainContent';
import HeaderBottom from './HeaderBottom';

const Header = () => {
  return (
    <header className="header header-outer">
        <div className="header-top">
            <div className="header-top-inner container">
                <Logo/>
                <HeaderNav/>
                <SignInAndUp/>
            </div>
        </div>

        <div className="header-main">
            <div className="header-main-inner container">
              <HeaderMainContent/>
            </div>
        </div>

        <div className="header-bottom">
          <div className="header-bottom-inner container">
            <HeaderBottom/>
          </div>
        </div>
    </header>
  )
}

export default Header;