import React from 'react';
import AboutImg from '../../images/about-img.png';
import BulletIco from '../../images/bullet.svg';

const ListItem = ({text}) => {
    return (
        <div className="about-right__list-item">
            <img 
                className="about-right__list-item-bullet" 
                src={BulletIco} alt="bulletIco"
            />
            {text}
        </div>
    )
}

const AboutSection = () => {
    return (
        <section className="about about-outer">
            <div className="about-inner container">
                <div className="about-left">
                    <img src={AboutImg} alt="about-img"/>
                </div>
                <div className="about-right">
                    <div className="about-right__title">
                        We Provide Many Features You Can Use
                    </div>
                    <div className="about-right__subtitle">
                        You can explore the features that we provide with fun 
                        and have their own functions each feature.
                    </div>
                    <div className="about-right__list">
                        <ListItem text={"Powerfull online protection."}/>
                        <ListItem text={"Internet without borders."}/>
                        <ListItem text={"Supercharged VPN"}/>
                        <ListItem text={"No specific time limits."}/>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default AboutSection;