import React from 'react';
import Logo from '../Global/Logo';
import Facebook from '../../images/Facebook.svg';
import Instagram from '../../images/Instagram.svg';
import Twitter from '../../images/Twitter.svg';

import {product, earnMoney, engage} from './data';

const FooterList = ({list}) => {
    return (
        <ul className="listWrap">
            {list.map((text, index) => 
                <li key={index} className="listWrap__item">
                    <a 
                        className="listWrap__item-link" 
                        href="/"
                    >
                        {text}
                    </a>
                </li>
            )}
        </ul>
    )
}

const Footer = () => {
    return (
        <footer className="footer footer-outer">
            <div className="footer-inner container">
                <div className="footer__left">
                    <div className="footer__left-logo">
                        <Logo/>
                    </div>
                    <div className="footer__left-text">
                        <span className="bolderText">LaslesVPN </span>
                            is a private virtual network that  
                            has unique features and has high security.
                    </div>
                    <div className="footer__left-socials">
                        <a href="/"><img src={Facebook} alt="fb-ico"/></a>
                        <a href="/"><img src={Twitter} alt="twitter-ico"/></a>
                        <a href="/"><img src={Instagram} alt="instagram-ico"/></a>
                    </div>
                    <div className="footer__left-copyright">
                        ©2020Lasles<span className="bolderText">VPN</span> 
                    </div>
                </div>
                <div className="footer__right">
                    <div className="footer__right-inner">
                        <div className="footer__right-col">
                            <FooterList list={product}/>
                        </div>
                        <div className="footer__right-col">
                            <FooterList list={engage}/>
                        </div>
                        <div className="footer__right-col">
                            <FooterList list={earnMoney}/>
                        </div>
                    </div>
                </div>
            </div>
        </footer>
    )
}

export default Footer;