import React from 'react';
import GlobalIMG from '../../images/global.png';

const GlobalNetworkSection = () => {
    return (
        <section className="globalSection globalSection-outer">
            <div className="globalSection-inner container">
                <div className="globalSection-title">
                    Huge Global Network of Fast VPN
                </div>
                <div className="globalSection-subtitle">
                    See <span className="bolderText">LaslesVPN</span> everywhere to make 
                    it easier for you when you move locations.
                </div>
                <div className="globalSection-content">
                    <img
                        className="globalSection-content-img"
                        src={GlobalIMG} 
                        alt="img"
                    />
                </div>
            </div>
        </section>
    )
}

export default GlobalNetworkSection;