import React from 'react';
import LinkButton from '../Global/LinkButton';

const SignInAndUp = () => {
    return (
        <div className="header-top__right">
            <div className="header-top__right-signIn">
               <a className="header-top__right-signIn-link" href="/">
                   Sign In
               </a>
            </div>
            <div className="header-top__right-signUp">
                <LinkButton text={"Sign Up"}/>
            </div>
        </div>
    )
}

export default SignInAndUp;