import React from 'react';
import UserIco from '../../images/user.svg';
import LocationIco from '../../images/location.svg';
import ServerIco from '../../images/Server.svg';

const HeaderBottomItem = ({ico, num, text}) => {
    return (
        <div className="header-bottom__item">
            <img src={ico} alt="ico"/>
            <div className="header-bottom__item-info">
                <div className="header-bottom__item-info-num">
                    {num}+
                </div>
                <div className="header-bottom__item-info-text">
                    {text}
                </div>     
            </div>
        </div>
    )
}

const HeaderBottom = () => {
    return (
        <>
            <HeaderBottomItem ico={UserIco} num={90} text={"Users"}/>
            <HeaderBottomItem ico={LocationIco} num={30} text={"Locations"}/>
            <HeaderBottomItem ico={ServerIco} num={50} text={"Servers"}/>
        </>
    )
}

export default HeaderBottom;